import React, {useState} from "react";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';

//  : https://teratail.com/questions/264506

type Props = { pileUpProblem: (url: string) => void }

const Form: React.FC<Props> = ({ pileUpProblem }) => {
    /*
    @param pileUpProblem: (url: string) => void
        url
        url
    */

    // 
    // valueonChangerendervalue
    const [value, setValue] = useState('');


    const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        //const urlForPiledUp: string = data.get('urlForPiledUp') as string;
        // 
        if (value.trim() === '')  return;
        // 
        pileUpProblem(value);
        // 
        setValue('');
    };


    return (
        <Typography
              component="h1"
              variant="h2"
              align="center"
              color="text.primary"
              gutterBottom
        >
            <Box component={"form"} onSubmit={handleSubmit} noValidate sx={{ mt: 10 }}>
                <TextField
                    //align="center" 
                    name="urlForPiledUp"
                    label="URL"
                    margin="normal"
                    variant="standard"
                    autoFocus
                    //  'value' 
                    onChange={e => setValue(e.currentTarget.value)}
                    value={value}
                />
            </Box>
        </Typography>
    );
}

export default Form